import React from 'react'
import Link from 'gatsby-link'
import Pagination from '../components/pagination'
import link from '../utils/link';

const Post = ({ node }) => {
  const tags = node.frontmatter.tags.map(tag => (
    <Link to={link.blogTag(tag)} key={tag} className='mx-1'>#{tag} </Link>
  ))
  return (
    <div key={node.id}>
      <div>{node.frontmatter.date}</div>
      <h2>
        <Link  to={link.blogPage(node.fields.slug)} className="black-link">
          {node.frontmatter.title}
        </Link>
      </h2>
      <div>{node.frontmatter.description}</div>
      {tags}
      <hr className="my-4"/>
    </div>
  )
}

const List = ({ pathContext }) => {
  const {group, index, pageCount} = pathContext
  const postList = group.map(({ node }) => <Post node={node} key={node.id} />)
  return (
    <div>
      {postList}
      <div className="d-flex justify-content-center">
        <Pagination index={index} pageCount={pageCount} />
      </div>
    </div>
  )
}

export default List



