import React from 'react';
import Link from 'gatsby-link';
import Wrapper from '../components/wrapper'
import BlogList from '../components/blog-list';
import link from '../utils/link'

const Page = ({ pathContext }) => {
  const { additionalContext } = pathContext;
  const { tag, totalCount } = additionalContext;
  return (
    <Wrapper single>
      <h2>{totalCount} pos dilabeli dengan #{tag}</h2>
      <h5>Lihat <Link to={link.blogTag('')}>semua label</Link></h5>
      <hr className="my-5"/>
      <BlogList pathContext={pathContext} />
    </Wrapper>
  );
};

export default Page;



